'use server'

import bcrypt from 'bcryptjs'
import { db } from '@/lib/db'
import { users } from '@/lib/schema'
import { cookies } from 'next/headers'
import { redirect } from 'next/navigation'
import { eq } from 'drizzle-orm'

export async function registerUser(formData: FormData) {
  const email = formData.get('email') as string
  const password = formData.get('password') as string

  if (!email || !password) {
    return { error: 'Email and password are required' }
  }

  try {
    // Check if user exists
    const existingUser = await db.select().from(users).where(eq(users.email, email))

    if (existingUser.length > 0) {
      return { error: 'User already exists' }
    }

    // Check if this is the first user (make them admin)
    const totalUsers = await db.$count(users)
    const role = totalUsers === 0 ? 'admin' : 'user'

    // Hash password
    const passwordHash = await bcrypt.hash(password, 10)

    // Insert user
    const result = await db.insert(users).values({
      email,
      passwordHash,
      role,
    }).returning({ id: users.id })

    const userId = result[0].id

    // Set cookie for session
    const cookieStore = await cookies()
    cookieStore.set('user_id', userId.toString(), {
      httpOnly: true,
      secure: process.env.NODE_ENV === 'production',
      maxAge: 60 * 60 * 24 * 7 // 7 days
    })

    return { success: true, userId: userId.toString() }
  } catch (error) {
    console.error('Registration error:', error)
    return { error: 'Failed to register user' }
  }
}

export async function loginUser(formData: FormData) {
  const email = formData.get('email') as string
  const password = formData.get('password') as string

  if (!email || !password) {
    return { error: 'Email and password are required' }
  }

  try {
    // Get user
    const result = await db.select().from(users).where(eq(users.email, email))

    if (result.length === 0) {
      return { error: 'Invalid credentials' }
    }

    const user = result[0]
    const isValidPassword = await bcrypt.compare(password, user.passwordHash)

    if (!isValidPassword) {
      return { error: 'Invalid credentials' }
    }

    // Set cookie
    const cookieStore = await cookies()
    cookieStore.set('user_id', user.id.toString(), {
      httpOnly: true,
      secure: process.env.NODE_ENV === 'production',
      maxAge: 60 * 60 * 24 * 7
    })

    return { success: true, userId: user.id }
  } catch (error) {
    console.error('Login error:', error)
    return { error: 'Failed to login' }
  }
}

export async function logoutUser() {
  const cookieStore = await cookies()
  cookieStore.delete('user_id')
  redirect('/login')
}

export async function getCurrentUser() {
  const cookieStore = await cookies()
  const userId = cookieStore.get('user_id')?.value

  if (!userId) {
    return null
  }

  try {
    const result = await db.select().from(users).where(eq(users.id, parseInt(userId)))

    if (result.length === 0) {
      return null
    }

    return result[0]
  } catch (error) {
    console.error('Get user error:', error)
    return null
  }
}

export async function createUserByAdmin(formData: FormData) {
  const currentUser = await getCurrentUser()
  if (!currentUser || currentUser.role !== 'admin') {
    return { error: 'Unauthorized' }
  }

  const email = formData.get('email') as string
  const password = formData.get('password') as string
  const role = formData.get('role') as string || 'user'

  if (!email || !password) {
    return { error: 'Email and password are required' }
  }

  if (!['user', 'admin'].includes(role)) {
    return { error: 'Invalid role' }
  }

  try {
    // Check if user exists
    const existingUser = await db.select().from(users).where(eq(users.email, email))

    if (existingUser.length > 0) {
      return { error: 'User already exists' }
    }

    // Hash password
    const passwordHash = await bcrypt.hash(password, 10)

    // Insert user
    await db.insert(users).values({
      email,
      passwordHash,
      role,
    })

    return { success: true }
  } catch (error) {
    console.error('Create user error:', error)
    return { error: 'Failed to create user' }
  }
}

export async function getAllUsers() {
  const currentUser = await getCurrentUser()
  if (!currentUser || currentUser.role !== 'admin') {
    return { error: 'Unauthorized' }
  }

  try {
    const result = await db.select({
      id: users.id,
      email: users.email,
      role: users.role,
      createdAt: users.createdAt,
    }).from(users).orderBy(users.createdAt)

    return { success: true, users: result }
  } catch (error) {
    console.error('Get users error:', error)
    return { error: 'Failed to get users' }
  }
}

export async function updateUserRole(userId: number, newRole: string) {
  const currentUser = await getCurrentUser()
  if (!currentUser || currentUser.role !== 'admin') {
    return { error: 'Unauthorized' }
  }

  if (!['user', 'admin'].includes(newRole)) {
    return { error: 'Invalid role' }
  }

  try {
    await db.update(users)
      .set({ role: newRole })
      .where(eq(users.id, userId))

    return { success: true }
  } catch (error) {
    console.error('Update user role error:', error)
    return { error: 'Failed to update user role' }
  }
}